// Header Tablet

.header {
  background-color: #FFFFFF;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  margin: 0;
  z-index: 1000;
  &__responsive {
    img {
      width: 2rem; } }
  &__nav {
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &__logo {
    margin-right: 3.5rem;
    img {
      width: 5.25rem; } }
  &__menu {
    transition: .5s;
    padding: 2.5rem 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: #FFFFFF;
    position: fixed;
    margin: 0 1rem;
    top: 6rem;
    bottom: 1rem;
    right: 0;
    left: 0;
    border-radius: 1rem;
    box-shadow: 2px 4px 19px 4px #EAEAEA;
    opacity: 0;
    transform: translateX(-100vw);
    li {
      a {
        padding: 1rem 0 0 0;
        font-size: 1.25rem;
        font-weight: 500;
        color: #33435b; }
      hr {
        padding: 0;
        opacity: .25; } } }
  &__fondo {
    display: none; }
  &__close {
    display: none; }
  &__close02 {
    transform: translateY(0);
    opacity: 1; } }

