// Home Tablet
.blue-text {
  color: #769fda; }

.hero {
  margin-top: 5rem;
  &__flex {
    padding: 2rem;
    text-align: center;
    &-text {
      &--etiqueta {
        p {
          font-size: 1rem;
          display: inline-block;
          color: #769fda;
          text-transform: uppercase;
          border: 3px #769fda solid;
          border-radius: 10rem;
          padding: .25rem 1.5rem;
          font-weight: 400; } }
      &--title {
        h1 {
          font-size: 2rem;
          font-weight: 500; } }
      &--p {
        margin-bottom: 3rem; }
      &--button {
        button {
          color: #FFFFFF;
          font-weight: 500;
          display: flex;
          background-color: #84d5dc;
          padding: .75rem 1.25rem;
          border-radius: 1rem;
          border-color: transparent;
          transition: 1s;
          margin: 0 auto;
          &:hover {
            background-color: #33435b; }
          a {
            display: flex;
            color: #FFFFFF;
            align-items: center;
            p {
              margin: 0; }
            img {
              width: 1rem;
              height: 1rem;
              margin-left: 1.25rem; } } } } }
    &-img {
      margin: 3rem auto;
      z-index: 10;
      img {
        width: 100%; } } }
  &__background {
    display: none; } }

.info {
  display: none; }
.responsive {
  padding: 1rem;
  margin: 2rem 0;
  &__title {
    text-align: center;
    margin-bottom: 2rem;
    h2 {
      margin: 0;
      font-size: 2.25rem;
      font-weight: 500; } }
  &__card {
    margin-top: 1rem;
    border: 1px solid #EAEAEA;
    border-radius: 1rem;
    &-title {
      cursor: pointer;
      border-radius: 1rem;
      border-bottom: 1px solid #EAEAEA;
      display: flex;
      color: #33435b;
      padding: .25rem 1rem;
      align-items: center;
      justify-content: space-between;
      img {
        width: 1.25rem;
        height: 1.25rem;
        transform: rotate(0deg); } }
    &-desplegable {
      display: none;
      transition: .5s; }
    &-img {
      margin: 1rem;
      img {
        width: 100%;
        height: 20rem;
        object-fit: cover;
        border-radius: 1.5rem; } }
    &-text {
      text-align: center;
      margin: 1rem; } } }

.activoResponsive {
  border-radius: 1rem 1rem 0 0;
  border: 2px solid #e6ffff;
  display: flex;
  color: #33435b;
  background-color: #e6ffff;
  padding: .25rem 1rem;
  align-items: center;
  justify-content: space-between;
  img {
    transform: rotate(90deg); } }

.mostrarResponsive {
  display: block; }

