// Footer Tablet

.footer {
  background-color: #e6ffff;
  padding: 3rem 2rem;
  &__grid {
    text-align: left;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 1rem;
    grid-gap: 1.5rem; }
  &__logo {
    grid-column: 1/3;
    text-align: center;
    margin-bottom: 2rem;
    img {
      width: 8rem; } }
  &__links01 {
    color: #33435b;
    grid-column: 1/2;
    &-title {
      h4 {
        margin: 0;
        font-size: .8rem; } }
    &-text {
      font-size: .75rem; }
    &-subtitle {
      h4 {
        color: #769fda;
        font-size: .75rem;
        text-transform: uppercase;
        font-weight: 400; } }
    &-flex {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 3rem; } }
  &__rrss {
    margin-top: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      font-size: .75rem;
      color: #33435b; }
    &-icons {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-gap: 2rem;
      a {
        img {
          width: 1rem; } } } }
  &__links02 {
    grid-column: 2/3; }
  &__links04 {
    grid-row: 4/5;
    grid-column: 2/3; }
  &__links05 {
    grid-column: 1/3; } }
